import React, {useState,} from 'react';
import AwesomeSliderStyles from './itemDescription.css';
import axios from 'axios';
import stringify from 'qs-stringify';
import Paper from '@material-ui/core/Paper';
import { SRLWrapper } from "simple-react-lightbox";
import LazyLoad from 'react-lazy-load';
import Loader from 'react-loader-spinner';
import loc from '../../../static/loc.png';
import st from '../../../static/st.png';
import '../../../static/Grid.css';
import Button from '@material-ui/core/Button';
import LiveHelpIcon from '@material-ui/icons/LiveHelp';
import EventAvailableRoundedIcon from '@material-ui/icons/EventAvailableRounded';
import moment from 'moment';
import createHistory from 'history/createBrowserHistory';
import { usePromiseTracker } from "react-promise-tracker";
import { trackPromise } from 'react-promise-tracker';






 const LoadingSimilar = props => {
  const { promiseInProgress } = usePromiseTracker();
   return (
     promiseInProgress && 
    <div>
      <div
      style={{
        width: "100%",
        height: "100",
       display: "flex",
        justifyContent: "center",
        alignItems: "center",
        
     }}
   >    
     <Loader type="ThreeDots" color="#FF4949" height="100" width="100" />
      </div>
    </div>
  );  
 }

const history = createHistory();

const options = {
 
  buttons: {
    backgroundColor: "#1b5245",
    iconColor: "rgba(126, 172, 139, 0.8)",
    size : "60px",
    iconPadding : '0px;'
  },
 }
  

class ItemDescription extends React.Component{

  constructor(props) {
    super(props);
    this.state = {
      'uniqueId' : props.match.params.uid,
      'item' :    {},
      similar : [],
      time:  moment().unix(),
       redirect: false,

    }
    this.getItemDescription = this.getItemDescription.bind(this);
    this.handleItemClick = this.handleItemClick.bind(this);
  }

  getItemDescription(uniqueId){
    this.setState({'loading' : true});
    let item ={} ,similar = [];
     trackPromise( axios.get(`https://corsanywhereintwiff.herokuapp.com/http://imsammy.freevar.com/punit.php?item=`+ uniqueId)
      .then((response) => {

               item =  response.data.success ? response.data.item : "error";
               similar =  response.data.success ? response.data.similar : "error";
               
               this.setState({'item' : item, 'similar' : similar, 'loading' : false});



  }))
   
}

  

  componentDidMount() { 

     this.getItemDescription(this.state.uniqueId);
  
     this.intervalID = setInterval(
      () => this.tick(),
      1000
    );
}
    componentWillUnmount() {
    clearInterval(this.intervalID);
  }


 
 tick() {

    this.setState({
      time :  parseInt(this.state.item.estimatedWaitTime, 10) - moment().unix(),
    });

  }

 pad(num) {
    return ("0"+num).slice(-2);
}
 hhmmss(secs) {
  var minutes = Math.floor(secs / 60);
  secs = secs%60;
  var hours = Math.floor(minutes/60)
  minutes = minutes%60;
  return `${this.pad(hours)}:${this.pad(minutes)}:${this.pad(secs)}`;
  // return pad(hours)+":"+pad(minutes)+":"+pad(secs); for old browsers
}

handleItemClick(uniquekey) {
  this.setState({'uniqueId' : uniquekey});
  this.getItemDescription(uniquekey);

}

  render() {
  
  if(this.state.loading == true) { return(<LoadingSimilar/>)}
    
    return(
      <div>
        <div className='ItemDescription'>
       <Paper variant="outlined">
      <div className='row no-gutters'>

         
        <div className='col-lg-4'>
          <SRLWrapper>
          <div className='row no-gutters'>
            <div className='col-lg-10'>{/*style={{'background-color' : 'yellow'}}*/}
            <center>
              <div className='MainImg mx-auto'>
              <div className='fillerMainImg'>
              <LazyLoad>
                <img src="https://source.unsplash.com/user/erondu/300x300" alt='Touch To go back'/>
                </LazyLoad>
                </div>
              </div>
              </center>
            </div>
            <div className='col-xl-2'> {/*style={{'background-color' : 'black'}}*/}
            <div className='mx-auto'>
              <div className='row no-gutters thumbnails'>
               
                  <div className='thumb th1  '>
                  <div className='fillerThumbnailImg'>
                   <LazyLoad>
                   <img src="https://source.unsplash.com/user/erondu/400x400" width='50' hieght='50' alt='Touch To go back' />
                   </LazyLoad>
                   </div>
                  </div>
               
                
                  <div className='thumb th2 '>
                    <div className='fillerThumbnailImg'>
                   <LazyLoad>
                   <img src="https://source.unsplash.com/user/erondu/400x400" width='50' hieght='50' alt='Touch To go back' />
                   </LazyLoad>
                   </div>
                  </div>
               
               
                  <div className='thumb th3 '>
                   <div className='fillerThumbnailImg'>
                   <LazyLoad>
                   <img src="https://source.unsplash.com/user/erondu/400x400" width='50' hieght='50' alt='Touch To go back' />
                   </LazyLoad>
                   </div>
                  </div>
                
              
                  <div className='thumb th4 '>
                   <div className='fillerThumbnailImg'>
                   <LazyLoad>
                   <img src="https://source.unsplash.com/user/erondu/400x400" width='50' hieght='50' alt='Touch To go back' />
                   </LazyLoad>
                   </div>
                  </div>
                  
              </div>
              </div>
            </div>
          </div>
             </SRLWrapper>
        </div>
         
        <div className='col-lg-8'>
          <div className='row no-gutters'>
            <div className='col-6'>
             <div className='DescBody'>
             <div className='itemTitle'> {this.state.item.title} </div>
             <div className='itemBrand'> {this.state.item.brand} </div>
             <div className='itemState'><div> <img src={st}/></div> <div>{this.state.item.state}</div></div>
             <div className='itemCity'><img src={loc}/>{this.state.item.city}</div>
             <div className='itemPrice'>
               <div className="style-1">
              <del>
                <span className="itemCutoffPrice">{this.state.item.cutOffPrice}</span>
              </del>
              <ins>
                <span className="itemActualPrice">{this.state.item.price}/<sub>hr</sub></span>
              </ins>
            </div>
             
             </div>
             </div>
            </div>
          <div className='col-6 col-lg-12 DescBodyRight'>
            
            <div className='rentingState'>
            {this.state.item.available ==='1'
             && this.state.item.booked === '0' 
             && 
             <div className='availItem'> <div><EventAvailableRoundedIcon/></div> <div> Available</div> </div>}
             {this.state.item.available ==='0'
             && this.state.item.booked === '0' 
             && <div className='CheckAvail'><Button variant='contained' style={{'background' : '#FF4343', 'color' : 'white'}} disableElevation> <LiveHelpIcon/> Check </Button> </div>}
            {this.state.item.booked === '1'
            && <div className='rentNowBtn'> <Button variant="contained" disabled>{this.hhmmss(this.state.time)}</Button> </div>}
            {this.state.item.available ==='1' 
              && <div className='rentNowBtn'> <Button variant="contained" style={{'background' : 'green', 'color' : 'white'}} disableElevation>Rent Now</Button> </div>

            }
            </div>
          </div>
        </div>
        </div>
        
      </div>
        </Paper>
        </div>










      <div className='SimilarCol'>

    {this.state.similar && this.state.similar.map(item => {

    

      return (<div className='SimilarItem' onClick={this.handleItemClick.bind(this,item.uniquekey)}>

       <Paper variant="outlined">
        <div className='sItemBody'>
          <div className='simImg'>
             <div className='fillerSimilar'>
                   <LazyLoad>
                   <img  id={item.uniquekey} src="https://source.unsplash.com/user/erondu/400x400" width='190' height='150' alt='Touch To go back' />
                   </LazyLoad>
                   </div>
          </div>
           <div className='DescBodySim'  id={item.uniquekey}>
             <div className='itemTitle'> {item.title} </div>
             <div className='itemBrand'> {item.brand} </div>
             <div className='itemCity simcity'><img src={loc}/>{this.state.item.city}</div>
            </div>

        </div>
       </Paper>
    </div>)
})}
   
 </div>
 </div>

 )
  }
}



export default ItemDescription;